@media print {
  body {
    background: $primary-white-color;
    padding: 0;
  }

  .switch-container {
    display: none;
  }

  // .toolbar {
  //   display: none;
  // }

  .wrapper {
    display: flex;
    box-shadow: none;
  }

  .select {
    display: none;
  }

  //sidebar
  .sidebar {
    width: 330px;
  }

  .info-mob {
    display: none;
  }

  .contacts-container,
  .skills-container {
    margin: 30px 30px 0;
  }

  .skills-container {
    margin: 30px 30px 0;

    &--lang {
      margin-top: 10px;
      padding-top: 20px;
    }
  }

  //main info
  .about-me-container {
    padding: 30px 25px;
  }

  .info-container {
    margin-bottom: 20px;
  }

  .info-tablet {
    margin-bottom: 10px;
  }

  .projects-container {
    margin-bottom: 20px;
  }

  .projects-item {
    margin-left: 15px;
  }

  .dots {
    display: none;
  }

  .link-description {
    display: block;
  }

  #back-to-top {
    display: none;
  }
}
